(function() {
  'use strict';

  angular
    .module('rakutenPaDemo')
    .controller('Claim3Controller', Claim3Controller);

  /** @ngInject */
  function Claim3Controller($state, ClaimService, $http) {
    var vm = this;

    vm.polNo = ClaimService.polNo
    vm.claims = [];

    vm.text1 = 'Cycle Assist';
    vm.text2 =  'Policy no. ' + vm.polNo;

    vm.text3 = 'What would you like to claim for?';

    vm.text4 = 'No benefits have been added yet. Click here to add benefits.';

    vm.text5 = '+ Add a benefit';
    vm.btn1 = 'Complete';

    vm.edit = function(id) {
      $state.go('claim-4', {id: id})
    };

    vm.add = function () {
      $state.go('claim-4', {id: -1});
    };

    vm.proceed = function() {
      vm.btn1 ='Loading...'; 
      var attachment = ClaimService.ClaimBody;
      attachment.Header.ClaimItems[0].ClaimedAmount = parseInt(attachment.Header.ClaimItems[0].ClaimedAmount);
      var req = {
        method: 'POST',
        url: 'https://general.sonic.dev.coherent.com.hk/general/v2/claim/initiate',
        data: attachment
      };
      $http(req)
        .then(function (response) {
          console.log(response)  
          ClaimService.setClaimRef(response.data.ClaimRef);
          $state.go('claim-9');
        }, function (response) {
            console.log('error');
      });

    }

    function init() {
      if(ClaimService.claims.length > 0) {
        vm.claims = ClaimService.claims;
      }
    }

    init();
  }
})();

