(function() {
  'use strict';

  angular
    .module('rakutenPaDemo')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, $timeout, $window) {

    $log.debug('runBlock end');

    var unregister = $rootScope.$on('$stateChangeSuccess', function() {
      $timeout(function () {
        $window.scrollTo(0, 0);
      }, 10);
    });

    $rootScope.$on('$destroy', unregister);
  }

})();

